module.exports = "import { Component } from '@angular/core';\r\n\r\n@Component({\r\n  selector: 'nz-demo-collapse-custom',\r\n  template: `\r\n    <nz-collapse [nzBordered]=\"false\">\r\n      <nz-collapse-panel *ngFor=\"let panel of panels\" [nzHeader]=\"panel.name\" [nzActive]=\"panel.active\"\r\n        [ngStyle]=\"panel.customStyle\">\r\n        <p>{{panel.name}} content</p>\r\n      </nz-collapse-panel>\r\n    </nz-collapse>\r\n  `,\r\n  styles  : []\r\n})\r\nexport class NzDemoCollapseCustomComponent {\r\n  panels = [\r\n    {\r\n      active     : true,\r\n      disabled   : false,\r\n      name       : 'This is panel header 1',\r\n      customStyle: {\r\n        'background'   : '#f7f7f7',\r\n        'border-radius': '4px',\r\n        'margin-bottom': '24px',\r\n        'border'       : '0px'\r\n      }\r\n    },\r\n    {\r\n      active     : false,\r\n      disabled   : true,\r\n      name       : 'This is panel header 2',\r\n      customStyle: {\r\n        'background'   : '#f7f7f7',\r\n        'border-radius': '4px',\r\n        'margin-bottom': '24px',\r\n        'border'       : '0px'\r\n      }\r\n    },\r\n    {\r\n      active     : false,\r\n      disabled   : false,\r\n      name       : 'This is panel header 3',\r\n      customStyle: {\r\n        'background'   : '#f7f7f7',\r\n        'border-radius': '4px',\r\n        'margin-bottom': '24px',\r\n        'border'       : '0px'\r\n      }\r\n    }\r\n  ];\r\n}\r\n"