module.exports = "import { Component } from '@angular/core';\r\n\r\n@Component({\r\n  selector: 'nz-demo-collapse-mix',\r\n  template: `\r\n    <nz-collapse>\r\n      <nz-collapse-panel *ngFor=\"let panel of panels\" [nzHeader]=\"panel.name\" [nzActive]=\"panel.active\">\r\n        <p>{{panel.name}}</p>\r\n        <div *ngIf=\"panel.childPanel&&panel.childPanel.length>0\">\r\n          <nz-collapse>\r\n            <nz-collapse-panel *ngFor=\"let childPanel of panel.childPanel\" [nzHeader]=\"childPanel.name\"\r\n              [nzActive]=\"childPanel.active\">\r\n              <p>A dog is a type of domesticated animal. Known for its loyalty and faithfulness, it can be found as a welcome guest in many households across the world.</p>\r\n            </nz-collapse-panel>\r\n          </nz-collapse>\r\n        </div>\r\n      </nz-collapse-panel>\r\n    </nz-collapse>\r\n  `,\r\n  styles  : []\r\n})\r\nexport class NzDemoCollapseMixComponent {\r\n  panels = [\r\n    {\r\n      active    : true,\r\n      disabled  : false,\r\n      name      : 'This is panel header 1',\r\n      childPanel: [\r\n        {\r\n          active: true,\r\n          name  : 'This is panel header 1-1'\r\n        },\r\n        {\r\n          active: false,\r\n          name  : 'This is panel header 1-2'\r\n        }\r\n      ]\r\n    },\r\n    {\r\n      active  : false,\r\n      disabled: true,\r\n      name    : 'This is panel header 2'\r\n    },\r\n    {\r\n      active  : false,\r\n      disabled: false,\r\n      name    : 'This is panel header 3'\r\n    }\r\n  ];\r\n}\r\n"